.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Google Fonts */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Raleway:wght@400;500;600;700&family=Roboto+Slab:wght@400;500;600;700&display=swap");
/* Variables */
:root {
  /* --main-color: #edd4c6; */
  --text-color: #121212;
  --container-color: #fdf5f0;
  --bg-color: #fff;
  --text-alter-color: #27282b;
  --poppins-font: "Poppins", sans-serif;
  --font-family-1: "Raleway", sans-serif;
  --font-family-2: "Roboto Slab", serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-padding-top: 3rem;
  font-family: var(--font-family-2);
}
/* Custom Scroll Bar */
html::-webkit-scrollbar {
  width: 0.5rem;
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: #d1a670;
}
body {
  color: var(--text-color);
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
img {
  width: 100%;
}
section {
  padding: 3rem 0 2rem;
}
.container {
  max-width: 960px;
  margin: auto;
  width: 100%;
}
/* Header */
header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
header.shadow {
  background: var(--bg-color);
  box-shadow: 0 1px 4px hsl(0deg 4% 15% / 10%);
  transition: 0.4s;
}
.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}
.logo {
  font-size: 1.3rem;
  font-weight: 500;
  letter-spacing: 1px;
  color: var(--text-color);
}
/* Navbar */
.navbar {
  display: flex;
  align-items: center;
  column-gap: 1.5rem;
}

.navbar-link {
  position: relative;
  font-size: 1rem; /* Adjust font size as needed */
  font-weight: 500;
  color: var(--text-color);
  font-family: var(--poppins-font);
  text-decoration: none; /* Remove underline from links */
  transition: 0.3s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

.navbar-link::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0%;
  height: 2px;
  background: var(--text-color);
  transition: 0.3s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

.navbar-link:hover::after {
  width: 100%;
  transition: 0.3s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

/* Menu Icon */
.menu-icon {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 5px;
  cursor: pointer;
  z-index: 200;
  transition: 0.3s;
}

.menu-icon div {
  display: block;
  background: var(--text-color);
  height: 3px;
  width: 25px;
  transition: 0.3s;
}

.menu-icon.move .line1 {
  transform: rotate(-45deg) translate(-5px, 5px);
}

.menu-icon.move .line2 {
  opacity: 0;
}

.menu-icon.move .line3 {
  transform: rotate(45deg) translate(-5px, -5px);
}

/* Footer */
.footer {
  background-color: #f8f9fa; /* Example background color */
  padding: 2rem 0;
}

.footer-container {
  display: flex;
  justify-content: space-between;
}

.footer-box {
  flex: 1;
  padding: 0 1rem;
}

.footer-form {
  display: flex;
  flex-direction: column;
}

.footer-form-label {
  margin-bottom: 0.5rem;
}

.footer-form-input {
  padding: 0.5rem;
  margin-bottom: 0.5rem;
}

.footer-form-button {
  padding: 0.5rem;
  background-color: var(--primary-color); /* Define primary color in your CSS variables */
  color: #fff;
  border: none;
  cursor: pointer;
}

.footer-links {
  display: flex;
  justify-content: space-between;
}

.footer-column {
  flex: 1;
  padding: 0 1rem;
}

.footer-link {
  display: block;
  margin-bottom: 0.5rem;
  text-decoration: none;
  color: var(--text-color);
}

.footer-link:hover {
  text-decoration: underline;
}

.footer-copyright {
  text-align: center;
  padding: 1rem 0;
  background-color: #e9ecef; /* Example background color */
}



.btn {
  padding: 10px 24px;
  border-radius: 0.2rem;
  font-family: var(--poppins-font);
  transition: 0.3s all linear;
}

.explore-btn:hover {
  color: var(--bg-color);
  background: var(--text-color);
}
/* Home */
.home {
  min-height: 640px;
  background: var(--container-color);
  display: flex; 
  align-items: center;
  padding: 0px 28px;
  justify-content: space-between;
}
.home-img {
  display: none;
}
.home-data {
  display: flex;
  flex-direction: column;
}
.home-text {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}
.home-text h3 {
  font-size: 1.1rem;
  font-weight: 500;
  letter-spacing: 1px;
  font-family: var(--poppins-font);
}
.home-text h2 {
  font-size: 2.8rem;
  font-weight: 600;
  letter-spacing: 1px;
  font-family: var(--font-family-2);
}
.home-btns {
  display: flex;
  align-items: center;
  column-gap: 1.5rem;
  margin-top: 1.5rem;
}
.explore-btn {
  border: 1px solid var(--text-color);
  color: var(--text-color);
  border-radius: 0.2rem;
}
.home-bg {
  position: relative;
  /* width: 500px;
  height: 500px; */
  z-index: 2;
}
.home-bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.home-bg::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: -2rem;
  left: -2rem;
  border: 1px solid var(--main-color);
  background: transparent;
  z-index: -1;
}
.contact-info {
  display: inline-block;
  margin-top: 1.5rem;
}

/* About */
.about-text {
  text-align: center;
  margin-top: 4rem !important;
}
.heading {
  position: relative;
  display: inline-flex;
  font-size: 1.5rem;
  font-weight: 500;
  letter-spacing: 1px;
  font-family: var(--poppins-font);
}
.heading::after {
  content: "";
  position: absolute;
  right: -12px;
  width: 40px;
  height: 40px;
  bottom: 0;
  background: #faf0e4;
  z-index: -1;
  clip-path: polygon(
    50% 0%,
    83% 12%,
    100% 43%,
    94% 78%,
    68% 100%,
    32% 100%,
    6% 78%,
    0% 43%,
    17% 12%
  );
}
.about-text h1 {
  font-size: 1.8rem;
  letter-spacing: 1px;
  margin: 1rem 0;
}
.about-text p {
  max-width: 600px;
  margin: auto;
  font-size: 1.075rem;
  color: var(--text-alter-color);
  margin-bottom: 1.5rem;
}
.learn-more {
  display: inline-flex;
  align-items: self-end;
  color: var(--text-color);
}
.learn-more .bx {
  font-size: 1.2rem;
  margin-left: 0.4rem;
  transition: 0.5s all cubic-bezier(0.075, 0.82, 0.165, 1);
}
.learn-more:hover .bx {
  margin-left: 0.8rem;
  transition: 0.5s all cubic-bezier(0.075, 0.82, 0.165, 1);
}
.about-data {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, auto));
  gap: 2rem;
  margin-top: 2rem;
}
.about-box {
  position: relative;
  width: 100%;
  height: 280px;
}
.about-box::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 7px;
  left: 7px;
  border: 1px solid var(--main-color);
  background: transparent;
  z-index: -1;
}
.about-box img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.about-pics {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 15px;
  clip-path: circle(0% at 50% 50%);
  transition: 0.5s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

.heading {
  margin-bottom: 1rem;
}

/* Contact Us Form  */
.error {
  color: red;
  font-size: 0.8em;
}










